export class Item {
    constructor(id, createDate, creator, modifiedAt, modifiedBy, icon) {
        this.createAt = '04/04/2021';
        this.owner = 'An Tran Hoang';
        if (id)
            this.id = id;
        if (createDate)
            this.createAt = createDate;
        if (creator)
            this.owner = creator;
        if (modifiedAt)
            this.modifiedAt = modifiedAt;
        if (modifiedBy)
            this.modifiedBy = modifiedBy;
        if (icon)
            this.icon = icon;
    }
}
